.panelBackground {
  display: flex;
  position: fixed;
  background-color: rgba(0,0,0,0.8);
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  top: 0;
  left: 0;
}
.Panel {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}